@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@400;600;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital,wght@0,300;0,400;0,700;1,300;1,400&display=swap);
.header {
    width: 100%;
    background-color: var(--tertiary-background);
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-bottom: var(--secondary-text) 1px solid;
    height: var(--header-height);
}

.header * {
    color: var(--primary-text);
}

.header-logo,
.header-motto,
.header-burger {
    padding: 10px 15px;
}

.header-logo {
    display: flex;
    flex-direction: column;
    line-height: 1.2em;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    background: linear-gradient(135deg, var(--default-purple) 35%, var(--default-pink) 85%);
    padding: 5px 35px;
    border-radius: 50px 5px;
}

.header-title {
    text-transform: uppercase;
    font-family: "Roboto Slab", serif;
    font-size: 1.4em;
    font-weight: bold;
}

.header-subtitle {
    font-style: italic;
    font-size: .9em;
    font-weight: 300;
}

.header-motto {
    font-style: italic;
    font-weight: 300;
}

@media screen and (max-width: 900px) {
    .header-banner {
        justify-content: space-between;
    }
}

/*
NAVIGATION
*/

.header-nav {
    height: 4em;
}

.header-link {
    display: inline-flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
    padding: 10px;
    width: 5em;
    border-left: 1px solid var(--secondary-text);
}

.header-link > * {
    font-family: "Roboto Slab";
    margin-bottom: 5px;
    margin-left: auto;
    margin-right: auto;
}

.header-link > *:last-child {
    font-size: 1.2em;
}

.header-link:last-child {
    border-right: 1px solid var(--secondary-text);
}

.header-link-active { 
    box-shadow: inset 0px -10px 9px -7px var(--default-purple);
}

.header-burger {
    font-size: 1.5em;
}
.sidebar-nav {
    display: inline-flex;
    flex-direction: column;
    justify-content: flex-start;
    background-color: var(--tertiary-background);
    overflow: hidden;
    height: calc(100% - var(--header-height));
    width: 200px;
    position: absolute;
    right: 0;
    transition: width .5s ease-in-out;
    border-left: 1px solid var(--secondary-text);
}

.sidebar-nav * {
    color: var(--primary-text);
}

.closed {
    /* Chrome freaks out if you translate stuff of the page, so we are just doing width instead */
    width: 0;
    /* Gets rid of the slight border overhang, without having it blink out of existence on transition */
    transform: translateX(1px);
}

.sidebar-link {
    display: flex;
    padding: 20px;
    border-bottom: 1px solid var(--secondary-text);
    line-height: 1;
    font-family: "Roboto Slab";
}

.sidebar-link > span {
    margin-left: 20px;
}

.sidebar-link-active { 
    box-shadow: inset 10px 0px 11px -7px var(--default-purple);
}
.article-content {
    /* 
    Stolen from a css-tricks comment
    Uses font-size transition then opacity transition to fake a height transition
    */
    transition: font-size 500ms 0ms ease-in, opacity 500ms 500ms ease-in;
}


.article-content.unloaded {
    font-size: 0;
    opacity: 0;
}
.generic-article {
    padding: 20px;
    background-color: var(--secondary-background);
    max-width: 800px;
    width: 100%;
    border-radius: 8px;
    margin-left: auto;
    margin-right: auto;
}

.generic-article-header {
    display: flex;
    justify-content: space-between;
}

.generic-article-title {
    margin: 0;
}

.generic-article-date {
    align-self: center;
}

.generic-article-description {
    font-style: italic;
    font-weight: 300;
    font-size: .9em;
    margin: 0;
}

@media screen and (max-width: 500px) {
    .generic-article-header {
        display: inline;
        display: initial;
    }
}
.blog-link {
    width: 100%;
    border-radius: 8px;
    background-color: var(--secondary-background);
    padding: 20px;
    margin-bottom: 10px;
    display: flex;
    flex: 0 0 auto;
}

.blog-links {
    max-width: 800px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-direction: column;
}

.blog-link-info,
.blog-link-header {
    width: 100%;
}

.blog-link-header {
    display: flex;
    justify-content: space-between;
}

.blog-link-title {
    font-size: 1.2em;
    margin: 0;
    display: inline-block;
}

.blog-link-date {
    align-self: center;
}

.blog-link-description {
    font-style: italic;
    font-weight: 300;
    font-size: .9em;
    margin: 0;
}

.blog-link-icon {
    font-size: 2em;
    margin-right: 20px;
    margin-top: auto;
    margin-bottom: auto;
}

.blog-back-button {
    display: block;
    max-width: 800px;
    width: 100%;
    font-size: 1.2em;
    font-family: "Roboto Slab", serif;
    background-color: var(--secondary-background);
    border-radius: 8px;
    padding: 10px;
    margin-left: auto;
    margin-right: auto;
}

.blog-back-button > :first-child {
    margin-right: 10px;
}

.blog-back-button:first-child {
    margin-bottom: 10px;
}

.blog-back-button:last-child {
    margin-top: 10px;
}

#blog-bottom-back-button {
    display: none;
}

@media screen and (max-width: 500px) {
    .blog-link-date,
    .blog-link-icon {
        display: none;
    }
}
html, body, #root {
    height: 100%;
    width: 100%;
    font-size: 18px;
    line-height: 1.7;
}

body {
    margin: 0;
    background-color: #18191A;
    background-color: var(--primary-background);
    color: #E4E6EB;
    color: var(--primary-text);
    box-sizing: border-box;
    font-family: "Roboto Condensed";
    overflow-x: hidden;
}

body * {
    box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
    font-family: "Roboto Slab", serif;
}

a {
    text-decoration: none;
}

a:link, a:visited, a:hover, a:active {
    color: #E4E6EB;
    color: var(--primary-text);
}

.content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: -webkit-min-content;
    height: -moz-min-content;
    height: min-content;
    min-height: 100%;
    padding: 20px;
    width: 100%;
    transition: -webkit-filter .5s ease-in-out;
    transition: filter .5s ease-in-out;
    transition: filter .5s ease-in-out, -webkit-filter .5s ease-in-out;
}

.content-area {
    display: flex;
    justify-content: center;
    overflow-y: auto;
    height: calc(100% - 72px);
    height: calc(100% - var(--header-height));
}

.blur {
    -webkit-filter: blur(2px);
            filter: blur(2px);
}

/*
SCROLL BAR
*/
::-webkit-scrollbar {
    border-radius: 8px;
    background: #3A3B3C;
    background: var(--tertiary-background);
    box-shadow: inset 0 0 3px #B0B3B8;
    box-shadow: inset 0 0 3px var(--secondary-text);
}

::-webkit-scrollbar-thumb {
    background: #5e30a8;
    background: var(--default-purple);
    border-radius: 8px; 
    box-shadow: inset 8px 4px 4px -6px #c024b6; 
    box-shadow: inset 8px 4px 4px -6px var(--default-pink);
}


/* 
VARIABLES
*/

:root {
    --default-purple: #5e30a8;
    --default-pink: #c024b6;
    --primary-background: #18191A;
    --secondary-background: #242526;
    --tertiary-background: #3A3B3C;
    --primary-text: #E4E6EB;
    --secondary-text: #B0B3B8;
    --header-height: 72px;
}

