.header {
    width: 100%;
    background-color: var(--tertiary-background);
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-bottom: var(--secondary-text) 1px solid;
    height: var(--header-height);
}

.header * {
    color: var(--primary-text);
}

.header-logo,
.header-motto,
.header-burger {
    padding: 10px 15px;
}

.header-logo {
    display: flex;
    flex-direction: column;
    line-height: 1.2em;
    width: fit-content;
    background: linear-gradient(135deg, var(--default-purple) 35%, var(--default-pink) 85%);
    padding: 5px 35px;
    border-radius: 50px 5px;
}

.header-title {
    text-transform: uppercase;
    font-family: "Roboto Slab", serif;
    font-size: 1.4em;
    font-weight: bold;
}

.header-subtitle {
    font-style: italic;
    font-size: .9em;
    font-weight: 300;
}

.header-motto {
    font-style: italic;
    font-weight: 300;
}

@media screen and (max-width: 900px) {
    .header-banner {
        justify-content: space-between;
    }
}

/*
NAVIGATION
*/

.header-nav {
    height: 4em;
}

.header-link {
    display: inline-flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
    padding: 10px;
    width: 5em;
    border-left: 1px solid var(--secondary-text);
}

.header-link > * {
    font-family: "Roboto Slab";
    margin-bottom: 5px;
    margin-left: auto;
    margin-right: auto;
}

.header-link > *:last-child {
    font-size: 1.2em;
}

.header-link:last-child {
    border-right: 1px solid var(--secondary-text);
}

.header-link-active {
    -webkit-box-shadow: inset 0px -10px 9px -7px var(--default-purple); 
    box-shadow: inset 0px -10px 9px -7px var(--default-purple);
}

.header-burger {
    font-size: 1.5em;
}